.block-header-hero {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  // background-color: grey;
  background-image: url(../images/hero/bg-hero-mobile.png);

  @media screen and (min-device-pixel-ratio: 2),
    screen and (-webkit-min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/hero/bg-hero-mobile@2x.png);
  }
  @media screen and (min-width: 768px) {
    background-image: url(../images/hero/bg-hero-tablet.png);

    @media screen and (min-device-pixel-ratio: 2),
      screen and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/hero/bg-hero-tablet@2x.png);
    }
  }

  @media screen and (min-width: 1366px) {
    background-image: url(../images/hero/bg-hero-desktop.png);
    background-repeat: no-repeat;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/hero/bg-hero-desktop@2x.png);
    }
  }
}

.block-position {
  position: relative;
}
