.guarantee-section {
  padding-top: 25px;
  // padding-bottom: 80px;
  width: 100%;
  margin: 0 auto;
}
.guarantee-container {
  display: flex;
  flex-wrap: wrap;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 30px;

  background-color: #f1f1f1;
}
.guarantee-title {
  font-family: Montserrat;
  font-weight: 800;
  font-size: 26px;
  line-height: 1.3;
  text-align: center;
  letter-spacing: 0.03em;
  color: ($color-title);
  margin-bottom: 30px;
}
.guarantee-list-item {
  display: flex;

  width: 100%;
  height: 75px;
  border-left: 1px dashed #b9b9b9;
  padding-inline-start: 28px;
}

.guarantee-list-item:not(:last-child) {
  margin-bottom: 23px;
}
.guarantee-list-item:last-child {
  border: none;
}

.guarantee-list {
  padding-inline-start: 7px;
  transform: translateY(30px);
}

.guarantee-deskription {
  position: relative;
  font-family: Montserrat;
  font-style: normal;
  text-align: left;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.66;
  color: ($color-text-alternative);
  height: 75px;
  flex-grow: 1;
  transform: translateY(-20px);
}

.guarantee-deskription:before {
  position: absolute;
  left: -36px;
  content: '';
  background-image: url(../images/marker-fire.svg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 20px;
  width: 14.7px;

  fill: ($color-accent);
  display: inline-block;
}

.guarantee-hightligt {
  color: ($color-accent);
}

@media screen and(min-width:768px) {
  .guarantee-container {
    width: 100%;
    padding-left: 80px;
    padding-right: 50px;
    padding-top: 30px;
  }
  .guarantee-deskription {
    line-height: 2.33;
    height: 38px;
  }
  .guarantee-list {
    transform: translateY(0);
  }
  .guarantee-list-item {
    height: 38px;
    padding-inline-start: 32px;
  }
  .guarantee-list-item:not(:last-child) {
    margin-bottom: 20px;
  }

  .guarantee-deskription:before {
    left: -40px;
  }
}

@media screen and(min-width:1366px) {
  .guarantee-section {
    padding-top: 105px;
  }
  .guarantee {
    padding-top: 153px;
    // padding-bottom: 107px;
  }
  .guarantee-container {
    padding-left: 112px;
    padding-right: 112px;
    padding-top: 50px;
    padding-bottom: 45px;
    width: 100%;
  }
  .guarantee-title {
    font-size: 46px;
    margin-bottom: 50px;
  }
  .guarantee-deskription {
    font-size: 20px;
    line-height: 1.4;
  }
}
